<template>
  <aside class="pickup">
    <c-table
      class="box-card"
      v-model="APP.modelAsideQuery"
      :dsl="APP.dslAsideQuery[0].rows"
      :rules="APP.rules"
      :tableInfo="APP.dslAsideQuery[0]"
      ref="topTabe"
    >
      <tr slot="submit" class="query-btn-wrapper">
        <el-button @click="APP.getAsideList" class="query-btn">查询</el-button>
      </tr>
    </c-table>
    <c-pickup-tree :treeOptions="APP.treeOptions" @node-click="handleNodeClick"></c-pickup-tree>
  </aside>
</template>

<script>
export default {
  name: "AppRoot",
  inject: ["APP"],
  props: {},
  data() {
    return {};
  },
  methods: {
    handleNodeClick(data, node, vm) {
      this.$emit("clickAside", {
        data,
        node,
        vm
      });
    }
  },
  components: {
    CTable: () =>
      import(/* webpackChunkName: "FormTable" */ "@c/FormTable.vue"),
    CPickupTree: () => import(/* webpackChunkName: "PickupTree" */ "@c/PickupTree.vue")
  }
};
</script>

<style src="@c/styles/app.scss" lang="scss">
</style>
<style src="@c/styles/hqbj.scss" lang="scss">
</style>


